<template>
    <layout :showBack="isEdit" :title="`${isEdit ? '编辑' : '新增'}采购单信息`">
        <div class="edit-container">
            <span class="title">基本信息</span>
            <van-form @submit="onSubmit">
                <van-field v-model="pageData.createName"
                    name="创建人"
                    label="创建人"
                    readonly
                    :rules="rules.createName"/>
                <van-field v-model="pageData.createTime"
                    name="创建时间"
                    label="创建时间"
                    clearable
                    readonly
                    placeholder="请选择创建时间"
                    :rules="rules.createTime">
                    <template #button>
                        <van-button size="small" @click="timeShow = true" native-type="button" type="primary">选择时间</van-button>
                    </template>
                </van-field>
                <van-field v-model="pageData.projectName"
                    name="项目名称"
                    label="项目名称"
                    clearable
                    readonly
                    placeholder="请选择项目名称"
                    :rules="rules.projectName">
                    <template #button>
                        <van-button size="small" @click="projectShow = true" native-type="button" type="primary">选择项目</van-button>
                    </template>
                </van-field>
                <span class="title">材料信息</span>
                <van-field v-model="pageData.materialName"
                    name="材料名称"
                    label="材料名称"
                    clearable
                    readonly
                    placeholder="请选择材料名称"
                    :rules="rules.materialName">
                    <template #button>
                        <van-button size="small" @click="materialShow = true" native-type="button" type="primary">选择材料</van-button>
                    </template>
                </van-field>
                <van-field v-model="pageData.materialWeight"
                    name="采购数量"
                    label="采购数量"
                    placeholder="请输入采购数量"
                    :rules="rules.materialWeight"/>
                <van-field v-model="pageData.materialUnit"
                    name="计量单位"
                    label="计量单位"
                    placeholder="请选择计量单位"
                    :rules="rules.materialUnit">
                    <template #input>
                        <van-dropdown-menu>
                            <van-dropdown-item v-model="pageData.materialUnit" :options="unitOption" />
                        </van-dropdown-menu>
                    </template>
                </van-field>
                <van-field v-model="pageData.price"
                    name="采购单价"
                    label="采购单价"
                    placeholder="请输入采购单价"
                    :rules="rules.price"/>
                <span class="title">供应商信息</span>
                <van-field v-model="pageData.supplierName"
                    name="名称"
                    label="名称"
                    clearable
                    readonly
                    placeholder="请选择供应商"
                    :rules="rules.supplierName">
                    <template #button>
                        <van-button size="small" @click="supplierShow = true" native-type="button" type="primary">选择供应商</van-button>
                    </template>
                </van-field>
                <van-field v-model="pageData.supplierContact"
                    name="联系人"
                    label="联系人"
                    readonly
                    placeholder="请输入联系人"/>
                <van-field v-model="pageData.supplierPhone"
                    name="联系电话"
                    label="联系电话"
                    readonly
                    placeholder="请输入联系电话"/>
                <span class="title">审批信息</span>
                <van-field v-model="currentUser.name"
                    name="审批人"
                    label="审批人"
                    clearable
                    readonly
                    placeholder="请选择审批人"
                    :rules="rules.nextId">
                    <template #button>
                        <van-button size="small" @click="userShow = true" native-type="button" type="primary">选择审批人</van-button>
                    </template>
                </van-field>
                <van-field v-model="pageData.remark"
                    name="备注"
                    label="备注"
                    type="textarea"
                    placeholder="请输入备注"/>
                <div class="btn">
                    <van-button round block type="primary" native-type="submit">提交</van-button>
                </div>
            </van-form>
        </div>

        <!-- 时间选择器 -->
        <van-popup v-model="timeShow"
            position="bottom"
            :close-on-click-overlay="false"
            :style="{ height: '309px' }">
            <van-datetime-picker v-model="date"
                @cancel="timeShow = false"
                @confirm="timePickEvt"
                type="datehour"
                title="选择创建时间"/>
        </van-popup>

        <van-popup v-model="userShow"
            position="bottom"
            :style="{ height: '100%' }">
            <user-picker v-model="currentUser"/>
        </van-popup>

        <van-popup v-model="supplierShow"
            position="bottom"
            :style="{ height: '100%' }">
            <supplier-picker @pickEvent="pickSupplierEvt"/>
        </van-popup>

        <van-popup v-model="materialShow"
            position="bottom"
            :style="{ height: '100%' }">
            <material-picker @pickEvent="pickMaterialEvt"/>
        </van-popup>

        <van-popup v-model="projectShow"
            position="bottom"
            :style="{ height: '100%' }">
            <project-picker @pickEvent="pickProjectEvt"/>
        </van-popup>
    </layout>
</template>

<script>
    import { purchaseAddApi, purchaseInfoApi, purchaseSaveApi } from '../../apis/PurchaseApi'
    import { formatDate } from '../../util'

    export default {
        watch: {
            currentUser(val) {
                this.pageData.nextId = val.id
                this.userShow = false
            }
        },
        data() {
            return {
                projectShow: false,
                materialShow: false,
                supplierShow: false,
                currentUser: {},
                userShow: false,
                timeShow: false,
                date: '',
                isEdit: false,
                pageData: {
                    id: '',
                    nextId: [],
                    createId: '',
                    createName: '',
                    createTime: '',
                    projectId: '',
                    projectName: '',
                    materialId: '',
                    materialName: '',
                    materialUnit: '',
                    materialWeight: '',
                    price: '',
                    supplierId: '',
                    supplierName: '',
                    supplierContact: '',
                    supplierPhone: '',
                    remark: ''
                },
                rules: {
                    createName: [{required: true, message: '单据创建人'}],
                    createTime: [{required: true, message: '选择创建日期'}],
                    projectName: [{required: true, message: '选择项目'}],
                    materialName: [{required: true, message: '选择材料'}],
                    materialUnit: [{required: true, message: '选择计量单位'}],
                    materialWeight: [{required: true, message: '选输入数量'}],
                    price: [{required: true, message: '选输入单价'}],
                    supplierName: [{required: true, message: '选择供应商'}],
                    nextId: [{required: true, message: '选择审批人'}]
                },
                unitOption: [
                    {text: 'Kg', value: 'Kg'},
                    {text: '吨', value: '吨'},
                    {text: '方', value: '方'}
                ]
            }
        },
        computed: {
            userInfo() {
                return this.$store.getters['common/getUserInfo']
            }
        },
        async created() {
            let id = this.$route.query.id
            this.isEdit = !!id
            if (this.isEdit) {
                let result = await purchaseInfoApi(id)
                if (result.code !== 200) {
                    this.isEdit = false
                    this.$toast.fail('编辑数据信息不存在')
                    return
                }
                for (let name in this.pageData) {
                    if (name === 'nextId') {
                        this.pageData.nextId = result.data.nextId[0]
                    } else if (name === 'createTime') {
                        this.pageData.createTime = formatDate(result.data.createTime, 'yyyy-MM-dd hh:mm:ss')
                    } else if (name === 'remark') {
                        this.pageData.remark = result.data.remark[0].remark
                    } else {
                        this.pageData[name] = result.data[name] || ''
                    }
                }
            } else {
                this.pageData.createId = this.userInfo.id
                this.pageData.createName = this.userInfo.name
            }
        },
        methods: {
            timePickEvt(val) {
                this.pageData.createTime = formatDate(val, 'yyyy-MM-dd hh:mm:ss')
                this.timeShow = false
            },
            async onSubmit() {
                let obj = {
                    ...this.pageData,
                    nextId: [this.currentUser.id]
                }

                let result
                if (this.isEdit) {
                    result = await purchaseSaveApi(obj)
                } else {
                    result = await purchaseAddApi(obj)
                }
                if (result.code === 200) {
                    this.$toast.success(`${this.isEdit ? '编辑' : '新增'}采购单信息成功`)
                } else {
                    this.$toast.fail(`${this.isEdit ? '编辑' : '新增'}采购单信息失败`)
                }
            },
            pickSupplierEvt(row) {
                if (!!row) {
                    this.pageData.supplierId = row.id
                    this.pageData.supplierName = row.name
                    this.pageData.supplierContact = row.contact
                    this.pageData.supplierPhone = row.phone
                }
                this.supplierShow = false
            },
            pickMaterialEvt(row) {
                if (!!row) {
                    this.pageData.materialId = row.id
                    this.pageData.materialName = row.name
                }
                this.materialShow = false
            },
            pickProjectEvt(row) {
                if (!!row) {
                    this.pageData.projectId = row.id
                    this.pageData.projectName = row.name
                }
                this.projectShow = false
            }
        }
    }
</script>